.titleWrapp {
  width: 319px;
  margin-top: 36px;
}

.titleWrapp .title {
  color: #173d33;
  font-family: 'Oswald';
  font-size: 28px;
  font-weight: 400;
  letter-spacing: 0;
  line-height: 28px;
  width: 319px;
}

.textWrapp {
  width: 320px;
  margin-top: 26px;
}

.textWrapp .text {
  color: #173d33;
  font-family: 'Fira Sans-Regular';
  font-size: 16px;
  font-weight: 400;
  letter-spacing: -0.64px;
  line-height: normal;
  text-align: justify;
  width: 320px;
}

.frame {
  align-items: flex-start;
  display: flex;
  flex-wrap: wrap;
  gap: 24px 20px;
  position: relative;
  width: 320px;
  margin-top: 36px;
}

.frame .group {
  height: 197px;
  position: relative;
  width: 152px;
}

.frame .overlapGroup {
  background-color: #eaedf1;
  height: 197px;
  position: relative;
  width: 148px;
}

.frame .rectangle {
  background-color: #97d28b;
  height: 1px;
  left: 12px;
  position: absolute;
  top: 70px;
  width: 124px;
}

.frame .textWrapper {
  color: #173d33;
  font-family: 'Oswald';
  font-size: 16px;
  font-weight: 400;
  left: 36px;
  letter-spacing: 0;
  line-height: 24px;
  position: absolute;
  top: 13px;
  white-space: nowrap;
}

.frame .div {
  color: #173d33;
  font-family: 'Fira Sans-Regular';
  font-size: 14px;
  font-weight: 400;
  left: 12px;
  letter-spacing: -0.56px;
  line-height: normal;
  position: absolute;
  text-align: justify;
  top: 83px;
  width: 124px;
}

.frame .overlapWrapper {
  height: 197px;
  margin-right: -4px;
  position: relative;
  width: 152px;
}

.icons {
  height: 16px !important;
  left: 12px !important;
  position: absolute !important;
  top: 17px !important;
  width: 16px !important;
}
